@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  margin: 5px;
}

:root {
  --byte-grey: #929292;
  --byte-orange: #f4a41b;
  --byte-gold: #efc326;
  --shadow-green: rgb(32, 37, 32);
}

.page-title {
  grid-area: title;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.page-title-container {
  position: relative;
  /* top: 0; */
  background-color: orange;
  width: min(650px, 60%);
  min-width: 400px;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
  /* Label Settings */
  color: white;
}

.page-subtitle-container {
  position: relative;
  top: -12px;
  background-color: white;
  border-radius: 15px;
  border-color: transparent;
  width: min(400px, 40%);
  min-width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* Label Settings */
  font-weight: bolder;
  font-size: 1.2rem;
  font-family: Arial;
  color: var(--byte-orange);
  box-shadow: 0px 4px 6px rgb(0, 0, 0, 0.2);
}

.entire-page {
  display: grid;
  height: 100%;
  padding: 0px 5vw 30px 5vw;
  margin-top: 75px;
  grid-template-columns: 1fr;
  grid-template-rows: 150px auto;
  grid-template-areas:
    "title"
    "body";
}

.hero-container {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
}
.home-hero {
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;
  padding-left: 2.5rem;
  border-left: 5px solid yellowgreen;
}

.download > h4 {
  margin-bottom: 0;
}
.download-line {
  width: 0%;
  height: 2px;
  background-color: white;
  transition: all 100ms ease;
}

.download:hover .download-line {
  width: 100%;
}

.login-divider {
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-divider-line {
  width: 88px;
  height: 3px;
  background-color: white;
  margin: 5px 10px;
}

.page-content-container {
  grid-area: body;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.diet-button-section {
  display: grid;
  grid-template-columns: repeat(auto-fill, 180px);
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
  width: 80%;
  max-width: 750px;
  margin: auto;
  border: 4px dashed var(--byte-orange);
  border-radius: 15px;
  padding: 30px;
}

.diet-side-menu-buttons {
  position: fixed;
  right: 20px;
  top: 100px;
}

.diet-row {
  display: flex;
  flex-direction: row;
  justify-content: center;

  margin: 10px;
}

.load-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  height: auto;
  width: 20%;
}

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 60%;
  box-sizing: border-box;
}

.login-container label {
  color: var(--byte-orange);
  font-size: 1.5rem;
}

.login-container form {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 30px;
  box-sizing: border-box;
}

.login-input {
  width: 100%;
  margin: 10px;
  padding: 10px;
  font-size: 1.2rem;
  border-radius: 15px;
  box-sizing: border-box;
}

.contact-container {
  border-radius: 25px;
  color: white;
  height: 70vh;
  margin: 30px 40px 30px 40px;
  /* display: flex; */
  text-align: center;
  justify-content: center;
  align-items: center;
  font-family: "A Goblin Appears";
}

.contact-text {
  margin-top: 50px;
  line-height: 50px;
  text-align: left;
  margin-left: 200px;
}

canvas {
  border: 1px solid #d3d3d3;
  background-color: #000;
  box-sizing: border-box;
}

.game-menu {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
}

.game-directions {
  color: white;
}

.game-title {
  display: flex;
  align-items: center;
}

.game-title h4 {
  margin-left: 1rem;
}
.game-title h2 {
  font-family: "Press Start 2P";
}

code {
  background: ghostwhite;
  border-radius: 5px;
  padding: 0 3px;
  color: darkolivegreen;
}

.byte-button {
  min-width: 208px;
  height: 58px;
  background-color: white;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
  border-color: transparent;
  /* Label Settings */
  color: #ff5c00;
  font-size: 18px;
  font-family: "A Goblin Appears";
  text-decoration: none;
}

.byte-button:hover {
  cursor: pointer;
  background-color: #ff5c00;
  color: white;
}

.snackbar {
  visibility: hidden;
  min-width: 250px;
  margin-left: -125px;
  background-color: #333;
  color: #fff;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 1;
  left: 50%;
  bottom: 30px;
  font-size: 1rem;
}

.snackbar.show {
  visibility: visible; /* Show the snackbar */
  /* Add animation: Take 0.5 seconds to fade in and out the snackbar. 
   However, delay the fade out process for 2.5 seconds */
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

/* Animations to fade the snackbar in and out */

@-webkit-keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}
