.footer {
  grid-area: footer;
  height: max(10vh, 300px);
  color: var(--byte-grey);
  background-color: rgb(29, 29, 29);
  padding: 125px 60px 20px 60px;
  box-sizing: border-box;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
}

.footer-sectionheader-container {
  width: 343px;
  border-bottom: 2px #888888 solid;
}

.footer-sectionheader-title {
  font-family: "A Goblin Appears";
  margin: 0px 5px 2px 10px;
}

.footer-section-links {
  display: flex;
  flex-flow: column wrap;
  max-height: 100px;
  max-width: 40%;
  margin-left: 10px;
  margin-top: 5px;
}

.footer-social-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 100px;
  max-width: 120px;
  margin: auto;
}

.footer-social-links a {
  color: whitesmoke;
  text-decoration: none;
}

.footer-section-links p {
  margin: 2px 30px 2px 0px;
}

.profile-dropdown-container {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: 20px;
  color: #4d4d4d;
  font-family: "Arial";
}

.profile-dropdown-inner-container {
  display: flex;
  padding: 10px;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  background-color: #ffcd78;
  border-radius: 10px;
  width: 95%;
  box-sizing: border-box;
  text-align: center;
}

.profile-button {
  position: relative;
  width: 50px;
  height: auto;
  border-radius: 5px;
  cursor: pointer;
}

.profile-dropdown {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 65px;
  right: 5px;
  min-width: 200px;
  max-width: 250px;
  z-index: 1;
  padding: 10px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  background-color: whitesmoke;
  box-shadow: 0px 4px 4px #00000025;
}

.hidden {
  display: none;
}

.profile-dropdown #user {
  margin-bottom: 15px;
}

.small {
  font-size: 0.7rem;
}

.profile-inner-icon {
  width: 70px;
  height: auto;
}

.signout-button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  margin-top: 3px;
  width: 100%;
  border-radius: 8px;
  background-color: #ff9a61;
  color: #4d4d4d;
  cursor: pointer;
}

.hamburger-container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 32px;
  cursor: pointer;
  z-index: 2;
}

.burger-1,
.burger-2,
.burger-3 {
  height: 0.2rem;
  width: 30px;
  background-color: white;
  border-radius: 10px;
  transition: all 0.5s ease-in-out;
}

.burger-open.burger-1 {
  transform: rotate(45deg) translate(8px, 8px);
}
.burger-open.burger-2 {
  transform: translateX(32px) rotate(90deg);
  background-color: yellowgreen;
}
.burger-open.burger-3 {
  transform: rotate(-45deg) translate(8px, -8px);
}

.header {
  position: fixed;
  top: 8px;
  width: 100%;
  background-color: rgb(29, 29, 29, 0);
  display: flex;
  height: 69px;
  z-index: 1;
  box-sizing: content-box;
  transition: all 0.2s ease-out;
}
.header-fixed {
  position: fixed;
  top: 0;
  background-color: rgba(29, 29, 29, 0.97);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2);
}
.logo {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.logo-font {
  font-size: 1.6rem;
}
.navbar {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.fullnav {
  display: flex;
  flex-basis: 100%;
  justify-content: space-between;
}

.nav-container {
  display: flex;
  align-items: center;
}

.navlink {
  margin: 10px;
  text-decoration: none;
  font-weight: bolder;
  font-family: "Arial";
  font-size: 1rem;
  font-weight: normal;
  color: white;
}

.navlink-selected {
  color: var(--byte-orange);
}

.navlink:hover {
  color: var(--byte-orange);
}

.navlink-line {
  background-color: white;
  height: 2px;
  width: 0%;
  margin: auto;
  margin-top: 2px;
  transition: 0.25s ease-in-out;
}

.navlink:hover > .navlink-line {
  width: 80%;
}
.navlink-selected > .navlink-line {
  width: 80%;
}

.side-menu-full {
  position: absolute;
  z-index: 1;
  height: 0;
  width: 0;
  background-color: rgba(0, 0, 0, 0);
  transition: background-color 0.3s ease-in-out;
}

.side-menu-full.side-menu-underlay {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.4);
}

.side-container {
  position: fixed;
  z-index: 1;
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-end;
  right: 0;
  width: 0;
  max-width: 250px;
  height: 100vh;
  background-color: yellowgreen;
  transition: all 0.5s ease-in;
  overflow-x: hidden;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
}

.side-container.side-open {
  width: 50vw;
  max-width: 250px;
}

.side-container :first-child {
  margin-top: 80px;
}

.side-menu-item {
  font-size: 3rem;
  font-family: "Oswald", "sans-serif";
  color: white;
  margin: 0.5rem 0;
  margin-right: 30px;
}

body {
  overflow-x: hidden;
}
.goblin-text {
  font-family: "A Goblin Appears";
}

main {
  position: relative;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: flex-start;
  height: 100vh;
  /* background: linear-gradient(180deg, #efc326 0%, #ff5c00 100%); */
  background-color: #282c34;
}

main > .login-hero {
  color: white;
  display: flex;
  height: 100%;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

section {
  width: 100%;
  height: 100%;
  /* background-image: url("./assets/images/backgrounds/barn-graduate-1.JPG"); */
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: right center;
}

.fixed {
  position: fixed;
  top: 0;
  right: 17px;
  left: 0;
  bottom: 0;
}

.container {
  width: 90vw;
  max-width: 1100px;
  margin: 0 auto;
  height: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.clicker {
  position: relative;
  background-color: yellowgreen;
  border: none;
  padding: 0.5rem 20px;
  margin: 1rem 0px;
  border-radius: 20px;
  box-shadow: 0px 4px rgb(57, 77, 18);
  min-width: 120px;
  outline: none;
  color: white;
  font-size: 1.2rem;
  font-weight: normal;
  font-family: "Oswald", "sans-serif";
}
.clicker:hover {
  cursor: pointer;
  background-color: rgb(125, 165, 45);
}
.clicker:active {
  transform: translateY(3px);
  box-shadow: 0px 2px rgb(57, 77, 18);
}

/* Default Styles */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: "Oswald", "sans-serif";
  color: white;
  margin: 0.5rem 0;
}

p {
  font-family: "Arial";
}
h1 {
  font-size: 4rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.5rem;
}

a {
  text-decoration: none;
}

@font-face {
  font-family: "A Goblin Appears";
  src: local("A Goblin Appears"), url("/static/media/A Goblin Appears.4c9c1f1e.otf");
}

